.dropdown-toggle:before {
  content: '>';
}

.menumenu {
  list-style-type: none;
  padding-left: 0;
  display: flex;
}

.menumenu ul {
  list-style-type: none;
  padding-left: 0;
  position: absolute;
}

.menumenu ul ul {
  top: 0;
}

.menumenu > li {
  position: relative;
}

.menumenu > li:not(:last-child) {
  margin-right: 14px;
}

.menumenu > li > ul {
  left: -6000px;
}

.menumenu > li > ul.sub-menu-in, .menumenu > li > ul.sub-menu-out {
  left: 0 !important;
}

.menumenu > li ul {
  width: 240px;
}

.sub-menu-in,
.sub-menu-out {
  left: 6000px !important;
}

.sub-menu-in ul,
.sub-menu-out ul {
  display: none;
}

.sub-menu-in {
  animation: SubMenuIn 0.5s cubic-bezier(0.77, 0, 0.175, 1);
}

.sub-menu-out {
  animation: SubMenuOut 0.4s cubic-bezier(0.77, 0, 0.175, 1);
}

@keyframes SubMenuIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes SubMenuOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(10px);
  }
}
